html,
body {
  position: relative;
  font-family: "SinkinSansRg", Helvetica, Arial, sans-serif; }
  @media (max-width: 768px) {
    html,
    body {
      overflow-x: hidden; } }

body {
  padding-bottom: 110px; }

ul {
  padding-left: 0; }

a {
  color: #494949;
  transition: ease .3s; }
  a, a:hover, a:focus {
    text-decoration: none; }
  a:hover, a:focus {
    color: #9b59b6; }

p {
  font-size: 16px;
  line-height: 1.8;
  color: #7f7f7f; }

.header {
  padding: 30px 60px;
  /* border-bottom: 1px solid $lightest_gray_color; */ }
  .header__logo {
    width: 202px;
    float: left;
    margin-top: 15px; }
    @media (max-width: 768px) {
      .header__logo {
        position: absolute;
        top: 15px;
        left: 15px;
        margin-top: 0; } }

.menu {
  margin-top: 40px;
  list-style: none; }
  .menu__list-item {
    display: inline-block;
    position: relative; }
  .menu__link {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 14px;
    padding: 15px 0;
    text-align: center; }
    .menu__link i {
      transition: .3s; }
    @media (max-width: 1024px) {
      .menu__link {
        margin-right: 8px;
        margin-left: 8px;
        font-size: 12px; } }
    .menu__link:after {
      content: ' ';
      display: inline-block;
      position: absolute;
      bottom: -8px;
      left: 50%;
      transform: translateX(-50%);
      box-shadow: inset 3px 0px 3px 0px rgba(255, 255, 255, 0.4);
      width: 0;
      height: 2px;
      background-color: rgba(7, 161, 153, 0.5);
      transition: ease .3s; }
    .menu__link.no-after:after {
      display: none; }
    .menu__link:hover:after {
      width: 80%; }
    .menu__link:hover i {
      transform: rotate(-90deg); }
  .menu__btn {
    margin-right: 0;
    margin-left: 15px; }
  .menu__icon {
    display: block;
    position: relative;
    top: 0px;
    float: left;
    left: 15px;
    margin-right: 5px; }
    @media (max-width: 1024px) {
      .menu__icon {
        left: 8px; } }
    .menu__icon_submenu {
      font-size: 18px;
      position: relative;
      top: 2px;
      right: 0; }
  @media (max-width: 768px) {
    .menu {
      position: fixed;
      bottom: -100vh;
      left: 0;
      width: 100vw;
      padding: 15px;
      z-index: 9;
      background-color: #fff;
      height: 50vh;
      transition: .3s;
      overflow: auto;
      box-shadow: rgba(0, 0, 0, 0.3) 3px 0px 5px; }
      .menu__list-item {
        display: block; }
        .menu__list-item:nth-last-child(2) a.menu__link {
          border-bottom: none; }
          #footerMenu .menu__list-item:nth-last-child(2) a.menu__link {
            border-bottom: 1px solid #7f7f7f; }
        .menu__list-item [class^=icon-] {
          width: 55px;
          display: inline-block;
          text-align: center; }
      .menu__link {
        width: 100%;
        display: block;
        text-align: left;
        position: relative;
        border-bottom: 1px solid #7f7f7f;
        margin: 0;
        font-size: 16px;
        font-family: "SinkinSansBold", Helvetica, Arial, sans-serif; }
        #footerMenu .menu__link i {
          margin-right: 15px;
